<template>
  <div class="submit-form">
    <div v-if="!submitted">
      <div class="form-group">
        <label for="title">Title</label>
        <input
          type="text"
          class="form-control"
          id="title"
          required
          v-model="book.title"
          name="title"
        />
      </div>

      <div class="form-group">
        <label for="sex">Sex</label>
        <input
          class="form-control"
          id="sex"
          required
          v-model="book.sex"
          name="sex"
        />
      </div>

      <div class="form-group">
        <label for="firstname">firstname</label>
        <input
          class="form-control"
          id="firstname"
          required
          v-model="book.firstname"
          name="firstname"
        />
      </div>

      <div class="form-group">
        <label for="lastname">lastname</label>
        <input
          class="form-control"
          id="lastname"
          required
          v-model="book.lastname"
          name="lastname"
        />
      </div>

      <button @click="saveBook" class="btn btn-success">Submit</button>
    </div>

    <div v-else>
      <h4>You submitted successfully!</h4>
      <button class="btn btn-success" @click="newBook">Add</button>
    </div>
  </div>
</template>

<script>
import BookDataService from '../services/BookDataService';

export default {
  name: 'add-book',
  data() {
    return {
      /* here to change */
      book: {
        id: null,
        title: '',
        sex: '',
        firstname: '',
        lastname: '',
        street: '',
        house_nr: '',
        region: '',
        post_nr: '',
        state: '',
        country: '',
        email: '',
        phone_nr: '',
        mobile_nr: '',
        extra_infos: '',

        published: false,
      },
      submitted: false,
    };
  },
  methods: {
    saveBook() {
      var data = {
        title: this.book.title,
        sex: this.book.sex,
        firstname: this.book.firstname,
        lastname: this.book.lastname,
        street: this.book.street,
        house_nr: this.book.house_nr,
        region: this.book.region,
        post_nr: this.book.post_nr,
        state: this.book.state,
        country: this.book.country,
        email: this.book.email,
        phone_nr: this.book.phone_nr,
        mobile_nr: this.book.mobile_nr,
        extra_infos: this.book.extra_infos,
      };

      BookDataService.create(data)
        .then(response => {
          this.book.id = response.data.id;
          console.log(response.data);
          this.submitted = true;
        })
        .catch(e => {
          console.log(e);
        });
    },

    newBook() {
      this.submitted = false;
      this.book = {};
    },
  },
};
</script>

<style>
.submit-form {
  max-width: 300px;
  margin: auto;
}
</style>
